@import url(https://fonts.googleapis.com/css?family=Fauna+One%7COxygen:400,300,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}
html {
  font-size: 10px;
}
body {
  font-size: 14px;
  line-height: 1.42857143;
  margin: 0;
}
img {
  vertical-align: middle;
}
h1,
h2,
h3,
h4,
h5 {
  font-family: 'Fauna One';
  font-weight: 400;
  color: #008bf8;
}
.App {
  font-family: Oxygen, Helvetica, Arial, sans-serif;
}
.NoteForm {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 0 3rem;
  }
  .NoteForm .form-actions {
    padding-top: 1rem;
    margin-left: -2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-content: center;
            align-content: center;
  }
  .NoteForm button {
    border: none;
    background: none;
    padding: 0;
  }
  .NoteForm button[type=submit] {
    background-color: #008bf8;
    border-radius: 4px;
    color: #fff;
    font-size: 1.4rem;
    padding: 1rem;
  }
  .NoteForm button {
    color: #999;
    font-size: 2rem;
    margin: 0;
  }

  .NoteForm button:hover {
    color: black
  }
  .NoteForm form {
    margin: 0 auto;
    max-width: 80rem;
  }
  .NoteForm input[name=title] {
    border: none;
    font-size: 200%;
    font-family: Fauna One;
    color: #008bf8;
    font-weight: 400;
    width: 100%;
    outline: none;
  }
  .NoteForm textarea {
    border-color: #eee;
    width: 100%;
    height: calc(100vh - 140px);
    font-size: 1.3em;
  }
.NoteList {
    border-left: 1px solid #eee;
    border-right: 1px solid #eee;
    width: 30rem;
  }
  @media (max-width: 800px) {
    .NoteList {
      width: 20rem;
    }
  }
  .NoteList h3 {
    color: #999;
    text-transform: uppercase;
    font-size: 2rem;
    font-family: Oxygen;
    font-weight: 300;
    letter-spacing: 3px;
    margin: 20px 2rem;
  }
  .NoteList ul#notes {
    border-top: 1px solid #eee;
    overflow-y: scroll;
    height: calc(100vh - 72px);
    list-style: none;
    margin-top: 1em;
    padding: 0;
    width: 100%;
    color: #999;
  }
  .NoteList ul#notes a,
  .NoteList ul#notes a:active,
  .NoteList ul#notes a:hover,
  .NoteList ul#notes a:link,
  .NoteList ul#notes a:visited {
    color: inherit;
    text-decoration: none;
  }
  .NoteList ul#notes>li {
    height: 100px;
    font-size: 90%;
    cursor: pointer;
    overflow: hidden;
  }
  .NoteList ul#notes>a,
  .NoteList ul#notes>a * {
    -webkit-transition: background-color .1s ease-in-out, color .1s ease-in-out;
    transition: background-color .1s ease-in-out, color .1s ease-in-out;
  }
  .NoteList ul#notes li .note {
    border-top: 1px solid #eee;
    margin: 0 2rem;
    padding: 1rem 4px;
  }
  .NoteList ul#notes a:first-of-type li .note {
    border-top: none;
  }
  .NoteList ul#notes li:hover {
    background-color: #008bf8;
  }
  .NoteList ul#notes li:hover,
  .NoteList ul#notes li:hover .note * {
    color: #fff!important;
    text-decoration: none !important;
  }
  .NoteList ul#notes li:hover .note {
    border-color: transparent;
  }
  .NoteList ul#notes li .note-title {
    color: #4a4a4a;
    font-family: Fauna One;
    font-size: 120%;
    font-weight: 400;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;
  }
  .NoteList ul#notes li .note-body {
    height: 54px;
    overflow: hidden;
    margin-top: .5rem;
  }
  .NoteList ul#notes li .note-body * {
    font-size: 1em!important;
    margin: 0!important;
    padding: 0!important;
    color: #999!important;
    background: none;
    border: none;
  }
  .NoteList ul#notes li .note {
    display: block;
    height: 100px;
    padding: 1em;
    text-decoration: none;
  }
.SignIn {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background: url(http://unsplash.it/1500/1000?image=885&blur=50);
    background-size: cover;
    height: 100vh;
  }
  .SignIn main {
    text-align: center;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
  }
  .SignIn main h3 {
    font-family: "Fauna One";
    color: #fff;
    font-size: 3rem;
    margin-bottom: 2rem;
  }
  .SignIn p {
    color: white;
    font-family: "Fauna One";
    font-size: 1.7rem;
    font-weight: 300;
    width: 30em;
    margin: 0 auto;
    border-bottom: 1px solid rgba(255,255,255,0.4);
    padding-bottom: 2rem;
  }
  .SignIn button {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    border: 0;
    border-radius: 5px;
    cursor: pointer;
    margin: 2.5rem auto 1.2rem;
    padding: 20px;
    -webkit-transition: background 0.2s;
    transition: background 0.2s;
    font-family: 'helvetica neue', sans-serif;
    font-size: 20px;
    font-weight: 200;
  }
  .SignIn button + button {
    margin-top: 0;
  }
  .SignIn button.github {
    background-color: #444;
    color: white;
  }
  .SignIn button.github i.fab,
  .SignIn button.github svg{
    font-size: 3rem;
    margin-right: 0.5rem;
  }
  .SignIn button.google {
    background-color: white;
    color: #666;
  }
